import { Component, OnInit, ElementRef, ViewChild } from '@angular/core';
import { EndpointsService } from '../endpoints.service';
import {FormBuilder, FormGroup, Validators} from "@angular/forms";
 import {Router,CanActivate,ActivatedRouteSnapshot,RouterStateSnapshot} from '@angular/router';
import { Angular2Csv } from 'angular2-csv/Angular2-csv';
import * as $ from 'jquery';

window["$"] = $;
window["jQuery"] = $;
@Component({
  selector: 'tox-ventas',
  templateUrl: './ventas.component.html',
  styleUrls: ['./ventas.component.scss']
})
export class VentasComponent implements OnInit {
 globalImage='http://50.116.17.150:3000/';
 form: FormGroup;
  @ViewChild('fileInput') fileInput: ElementRef;
rows = [];
periodo = [];
  temp = [];
 dataPeriodos={};
    rowsUsuarios = [];
dataImagen='';
dataFile='';
url='';
  categorias = [];
  selected = [];
  editar = false;
  claseSeleccionada = {};
  nuevaClase ={}; 
   cu = JSON.parse(localStorage.getItem('currentUser'));
   public loading = false;
public isCollapsed = false;



fechaFiltro = {};
fechaFiltro2 = {};
fechaFiltro4 = {};

    rowsHorarios = [];

selectedHorario = [];
  columnsHorarios: any[] = [
    { prop: 'idReservaClase', name:'#ID Reserva'} , 
    { prop: 'soloFecha',  name: 'Fecha Clase' }, 
        { prop: 'soloHora',  name: 'Hora Clase' }, 
       { prop: 'estado',  name: 'Estado' }, 
         { prop: 'usuariosAnotados',  name: 'Usuarios Anotados' }, 


  ];


  originalData = [];

  columns: any[] = [
    { prop: 'nombre',  name: 'Nombre' }, 
    { prop: 'valor',  name: 'Valor' }
  ];


  constructor(public endpointsService: EndpointsService,public fb: FormBuilder,private router:Router) { 
      this.createForm();

      if(this.cu[0]['tipo'] !== 3){
  console.log('usuario no deseado');
  this.router.navigate(['/dashboard']);
}


var TodayDate = new Date();
var dd = TodayDate.getDay();
var mm = TodayDate.getMonth()+1;
var mm2 = TodayDate.getMonth();
var yy = TodayDate.getFullYear();

this.fechaFiltro = {year: yy, month: mm2, day: dd};
this.fechaFiltro2 = {year: yy, month: mm, day: dd};
this.fechaFiltro4 = {year: yy, month: mm2, day: dd};



      }


  createForm() {
    this.form = this.fb.group({
      imageU: null
    });
  }

  ngOnInit() {

this.getStaff(this.fechaFiltro,this.fechaFiltro2);


  }

getD1(id){
if(this.periodo[id]){
	return (Object.is(!this.periodo[id].length,1) && Object.is(this.periodo[id][0]["estadoAsignado"],1));
}
else{return false;}
}

getD2(id){
if(this.periodo[id]){
	return (Object.is(this.periodo[id].length,1) && Object.is(this.periodo[id][0]["estadoAsignado"],2));
}
else{return false;}
}


  getDia(dd) {


if(dd){return dd.split('-')[2].split('T')[0]}
else{return '-'}

  }

  pad2(number) {
      return (number < 10 ? '0' : '') + number;
  }

getDataFix(data){
	if(data){return '$'+data.toFixed(2);}
	else{return '$0';}
	
}
  getStaff(fecha1,fecha2){
 this.loading = true;
  var emv= 	{ fecha1: fecha1.year+'-'+this.pad2(fecha1.month)+'-'+this.pad2(fecha1.day),
              fecha2: fecha2.year+'-'+this.pad2(fecha2.month)+'-'+this.pad2(fecha2.day)};

        this.endpointsService.getVentas(emv)

    .then((data:any) => {   

      console.log(data);
          this.temp = [...data];
      this.rows = data;
       this.loading = false;
      this.originalData = data;
     });


  }



  updateFilter(event) {
    const val = event.target.value.toLowerCase();

    // filter our data
    const temp = this.temp.filter(function(d) {
      return d.nombre.toLowerCase().indexOf(val) !== -1 || !val;
    });

    // update the rows
    this.rows = temp;
    // Whenever the filter changes, always go back to the first page
   // this.table.offset = 0;
  }



  cargaUsuarios(id){

  this.endpointsService.getUsuariosAnotadosClase({idReserva:id})
    .then((data:any) => {    
      //console.log(data);
      this.rowsUsuarios = data;
     // this.originalData = data;
     });


  }



  onFileChange(event) {
    if(event.target.files.length > 0) {
      let file = event.target.files[0];
      //this.form.get('imageU').setValue(file);

    var reader = new FileReader();
    reader.onload = (event:any) => {
      this.dataImagen = event.target.result;
    }
    reader.readAsDataURL(event.target.files[0]);
    this.dataFile = file;
    }
  }


readUrl(event:any) {
  if (event.target.files && event.target.files[0]) {
    var reader = new FileReader();

    reader.onload = (event:any) => {
      this.url = event.target.result;
    }

    reader.readAsDataURL(event.target.files[0]);
  }
}


    prepareSave(empleado){

    let input = new FormData();
  
    input.append('imageU', this.dataFile);
    input.append('nombre', empleado.nombre);
    input.append('estado', empleado.estado);
    input.append('idCategoria', empleado.idCategoria);

    return input;
  }

  onSubmit() {
    const formModel = this.prepareSave(this.claseSeleccionada);
    console.log(formModel);

    this.endpointsService.subirImagen(formModel)
    .then((data:any) => {    
        console.log(data);
     });


    
  }

getCalen(fecha1,fecha2){

this.getStaff(fecha1,fecha2);


}

  guardarNuevaClase(){

 this.loading = true;
 console.log(this.nuevaClase);

      this.endpointsService.nuevaCategoria(this.nuevaClase)
    .then((data:any) => {    
      console.log(data);
       this.loading = false;

       alert('Guardado correctamente');
       this.editar = false;
       this.nuevaClase={};

     },  msg => { // Error
        console.log(msg);
        this.loading = false;
        });





  }

  
      cargaCategorias(){

        this.endpointsService.categoriasHome()
    .then((data:any) => {    
      console.log(data);
      this.categorias = data;
     });


  }



  onSelectHorario({ selectedHorario }) {
    console.log(this.selectedHorario[0]); 
   // this.rows = this.originalData;
   // this.test = JSON.parse(JSON.stringify(this.selected[0]));
   // this.claseSeleccionada = JSON.parse(JSON.stringify(this.selected[0]));
    this.cargaUsuarios(this.selectedHorario[0].idReservaClase);
   
  }



  onSelect({ selected }) {
    console.log(this.selected[0]);
    this.selectedHorario = []; 
   // this.rows = this.originalData;
   // this.test = JSON.parse(JSON.stringify(this.selected[0]));
    this.claseSeleccionada = JSON.parse(JSON.stringify(this.selected[0]));
    this.dataImagen = this.globalImage+this.claseSeleccionada['idFoto'];
   // this.cargaHorarios();
  }





  generarArchivo() {
   // console.log('Activate Event', event);

var nombre = this.fechaFiltro['year']+'-'+this.pad2(this.fechaFiltro['month'])+'-'+this.pad2(this.fechaFiltro['day'])+' / '+this.fechaFiltro2['year']+'-'+this.pad2(this.fechaFiltro2['month'])+'-'+this.pad2(this.fechaFiltro2['day']);


   var data = [
  {
    nombreCentro: "Centro",
    nombre: "Sucursal",
    cantCitas: "Citas",
    sumCitas: "Total",
    comision: "Comision",
    cantOferta: "Ofertas",
    costoOferta: "Total Ofertas",
     cantPaquete: "Paquetes", 
     costoPaquete: "Total Paquetes"
  }];

      this.rows.forEach((item)=>{

        var prov = {nombreCentro:item.nombreCentro,
                    nombre:item.nombre,
                    cantCitas: item.cantCitas || 0,
                    sumCitas: item.sumCitas || 0,
                    comision: item.comision || 0,
                    cantOferta: item.cantOferta || 0, 
                    costoOferta: item.costoOferta || 0,
                     cantPaquete: item.cantPaquete || 0,  
                    costoPaquete: item.costoPaquete || 0
                    };
           data.push(prov);         


        });
        

var options = {
title: 'Periodo : '+nombre,
showTitle: true,
};
 
new Angular2Csv(data, nombre, options);


  }


  onActivate(event) {
    console.log('Activate Event', event);
  }


  guardarEdicion(){
 this.loading = true;
 console.log(this.claseSeleccionada);


  this.endpointsService.editarParametro(this.claseSeleccionada)
    .then((data:any) => {    
      console.log(data);
       this.loading = false;
 
       alert('Editado correctamente');
       this.editar = false;

     },  msg => { // Error
        console.log(msg);
       // this.loading = false;
        });



  }



  onChangeColor2(er){
  console.log(er)
  this.claseSeleccionada['color'] = er;
  }


  onChangeColor(er){
  console.log(er)
  this.nuevaClase['color'] = er;
  }
  cancelarEdicion(){
  //  this.rows = this.originalData;
 // console.log(this.test);
  }



}



