  getPrecioDescuento(precio){

  var retorno = 0;


  if(this.citaSelected['descuento']){

    var descuento = this.citaSelected['descuento'];
    var precioN = precio;
    retorno = precioN - (precioN*(descuento/100));

  }
  else{
    retorno = precio;
  }




  return retorno.toFixed(2);

}



