  generarArchivo() {
   // console.log('Activate Event', event);

var nombre = 'Reporte-UsuariosApp';


   var data = [
  {
    nombre: "Nombre",
    email: "Email",
    telefono: "Movil",
    activas: "Activas",
    completadas: "Completas",
    exp: "Exp",
    estado: "Estado"
    }];

      this.rows.forEach((item)=>{

        var prov = {nombre:item.nombre,
                    email: item.email ||  '',
                    telefono: item.telefono || '',
                    activas: item.activas || 0,
                    completadas: item.completadas || 0, 
                    exp: (item.appexp+' / '+(item.exp || 0)),
                     estado: (item.estado == 1 ? 'Activo' : 'Inactivo')};

           data.push(prov);         


        });
        

var options = {
title: nombre,
showTitle: true,
};
 
new Angular2Csv(data, nombre, options);


  }



