
import { Component, OnInit,ElementRef,ViewChild } from '@angular/core';
import { Router,  NavigationExtras,ActivatedRoute } from '@angular/router';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { EndpointsService } from '../endpoints.service';

 @Component({
  selector: 'tox-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})


export class LoginComponent implements OnInit {
  @ViewChild('username') el:ElementRef;
  statuslogin:any;
  focusin: boolean = true;
  rForm: FormGroup;
  post:any;  
  usernameAlert:string="Please fill username";
  passwordAlert:string="Please fill password";
  loginAlert:string;
  loginError:boolean=false;
  returnUrl: string;
  constructor(
       private route: ActivatedRoute,
      private fb: FormBuilder,
      private authenticationservice:EndpointsService,    
      public router: Router
    ) {
    this.rForm = fb.group({
      'username' : [null, Validators.required],
      'password' : [null, Validators.required],
    });
   }
   ngOnInit() {
    this.authenticationservice.logout();
    this.returnUrl = this.route.snapshot.queryParams['returnUrl'] || '/';
}
 
  addPost(post) {
console.log(post);

         this.authenticationservice.login(post)
    .then((data:any) => {  
    console.log(data);

            if(data.length > 0)
          {
             if(data[0]['estado']==2 || data[0]['estadoCentro']==2){
              this.loginError = true
              this.loginAlert = 'Cuenta desactivada, por favor contacte a soporte';
             }
             else{
              localStorage.setItem( 'currentUser', JSON.stringify(data) );
              this.router.navigate([this.returnUrl]);
             }

          	 
          }else{
            this.loginError = true
            this.loginAlert = 'Credenciales incorrectas';
          }

     });

/*
   localStorage.setItem('currentUser', JSON.stringify(res.json().data));
   this.authenticationservice.login(post).subscribe(
      res => {
        if(res.status == true)
          {
             this.router.navigate([this.returnUrl]);
          }else{
            this.loginError = true
            this.loginAlert = res.message;
          }
      },
       err => {
        return err;
          
      }
    );
 */
  }
 
}


