  getPrecioDescuento(precio){

  var retorno = 0;


  if(this.claseSeleccionada['descuento']){

    var descuento = this.claseSeleccionada['descuento'];
    var precioN = precio;
    retorno = precioN - (precioN*(descuento/100));

  }
  else{
    retorno = precio;
  }




  return retorno.toFixed(2);

}
 
