import { Component, OnInit } from '@angular/core';
import { EndpointsService } from '../endpoints.service';
import {Router,CanActivate,ActivatedRouteSnapshot,RouterStateSnapshot} from '@angular/router';

import {NgbModal, ModalDismissReasons, NgbActiveModal} from '@ng-bootstrap/ng-bootstrap';

@Component({
  selector: 'tox-ofertas',
  templateUrl: './ofertas.component.html',
  styleUrls: ['./ofertas.component.scss']
})

export class OfertasComponent implements OnInit {

  cu = JSON.parse(localStorage.getItem('currentUser'));
  seccionActivas = localStorage.getItem('seccUBY');



  rows = [];
  rows2 = [];
    rowsUsuarios = [];
modalReference: any;
  categorias = [];
  selected = [];
  editar = false;
  dataParametros={};
  claseSeleccionada = {};
  nuevaClase ={}; 
   public loading = false;
public isCollapsed = false;


    rowsHorarios = [];

selectedHorario = [];
  columnsHorarios: any[] = [
    { prop: 'idReservaClase', name:'#ID Reserva'} , 
    { prop: 'soloFecha',  name: 'Fecha Clase' }, 
        { prop: 'soloHora',  name: 'Hora Clase' }, 
       { prop: 'estado',  name: 'Estado' }, 
         { prop: 'usuariosAnotados',  name: 'Usuarios Anotados' }, 


  ];


  originalData = [];

  columns: any[] = [
    { prop: 'nombre',  name: 'Servicio' }, 
    { prop: 'precioOferta',  name: 'Precio de Oferta' }, 
         { prop:'duracion',  name: 'Duracion' }, 
     { prop:'fechaCaducidad',  name: 'Fecha de caducidad' }, 
  ];


  constructor(public endpointsService: EndpointsService, private modalService: NgbModal, private router:Router) {


  if(this.cu[0]['tipo'] == 2){
if(!this.seccionActivas.split(',').includes("8")){
  console.log('usuario no deseado');
  this.router.navigate(['/dashboard']);

  }
}


 }

  ngOnInit() {
  	console.log('d');
this.cargaCategorias();
this.getPaquetesOfertas();




  }

  getPaquetesOfertas(){

        this.endpointsService.getPaquetes({idCentro:localStorage.getItem('idCentroBY')})
    .then((data:any) => {    
      console.log(data);
      this.rows = data['ofertas'];
           this.rows2 = data['paquetes'];
      //this.originalData = data;
     });


  }

  getCentroServicios(){

        this.endpointsService.getCentroServicios({idCentro:localStorage.getItem('idCentroBY')})
    .then((data:any) => {    
      //console.log(data);
      this.rows = data.servicios;
      this.originalData = data;
     });


  }




  cargaHorarios(){

  this.endpointsService.cargaHorariosClase({idClase:this.claseSeleccionada['idClase']})
    .then((data:any) => {    
      //console.log(data);
      this.rowsHorarios = data;
     // this.originalData = data;
     });


  }
  getFecha(va){

 if(va){return va.split('T')[0];}
 else {return ' - ';}


  }

  cargaUsuarios(id){

  this.endpointsService.getUsuariosAnotadosClase({idReserva:id})
    .then((data:any) => {    
      //console.log(data);
      this.rowsUsuarios = data;
     // this.originalData = data;
     });


  }
  


openVerticallyCentered(content) {
      this.modalReference = this.modalService.open(content);
  }
openVerticallyCentered2(content2) {
      this.modalReference =  this.modalService.open(content2);
  }


  guardarNuevaClase(){

 this.loading = true;
 console.log(this.nuevaClase);
this.nuevaClase['idCentro'] = localStorage.getItem('idCentroBY');
      this.endpointsService.nuevoServicio(this.nuevaClase)
    .then((data:any) => {    
      console.log(data);
       this.loading = false;
       this.getCentroServicios();
       alert('Guardado correctamente');
       this.editar = false;
       this.nuevaClase={};

     },  msg => { // Error
        console.log(msg);
        this.loading = false;
        });





  }

  
      cargaCategorias(){

        this.endpointsService.getParametrosFF()
    .then((data:any) => {    
      console.log(data);
      this.dataParametros = data[0];
     });


  }

       contratarOferta(){

       var dataE={
       idCentro:localStorage.getItem('idCentroBY')
       }

        this.endpointsService.contratarOferta(dataE)
    .then((data:any) => {    
      console.log(data);
 this.getPaquetesOfertas();
      if(data.insertId && data.insertId > 0){
       alert('Oferta agregada correctamente, Ya puedes configurar tus servicios');
       this.modalReference.close();
      }
      else{
      alert('Ha ocurrido un error');
      this.modalReference.close();
      }
      //this.dataParametros = data[0];
     
     });


  }

       contratarPaquete(){

       var dataE={
       idCentro:localStorage.getItem('idCentroBY')
       }

        this.endpointsService.contratarPaquete(dataE)
    .then((data:any) => {    
      console.log(data);
 this.getPaquetesOfertas();
      if(data.insertId && data.insertId > 0){
       alert('Paquete agregado correctamente, Ya puedes configurar tus paquetes');
       this.modalReference.close();
      }
      else{
      alert('Ha ocurrido un error');
      this.modalReference.close();
      }
      //this.dataParametros = data[0];
     
     });


  }


  onSelectHorario({ selectedHorario }) {
    console.log(this.selectedHorario[0]); 
   // this.rows = this.originalData;
   // this.test = JSON.parse(JSON.stringify(this.selected[0]));
   // this.claseSeleccionada = JSON.parse(JSON.stringify(this.selected[0]));
    this.cargaUsuarios(this.selectedHorario[0].idReservaClase);
   
  }



  onSelect({ selected }) {
    console.log(this.selected[0]);
    this.selectedHorario = []; 
   // this.rows = this.originalData;
   // this.test = JSON.parse(JSON.stringify(this.selected[0]));
    this.claseSeleccionada = JSON.parse(JSON.stringify(this.selected[0]));
   // this.cargaHorarios();
  }

  onActivate(event) {
    console.log('Activate Event', event);
  }


  guardarEdicion(){
 this.loading = true;
 console.log(this.claseSeleccionada);

      this.endpointsService.editarServicio(this.claseSeleccionada)
    .then((data:any) => {    
      console.log(data);
       this.loading = false;
       this.getCentroServicios();
       alert('Editado correctamente');
       this.editar = false;

     },  msg => { // Error
        console.log(msg);
       // this.loading = false;
        });




  }



  onChangeColor2(er){
  console.log(er)
  this.claseSeleccionada['color'] = er;
  }


  onChangeColor(er){
  console.log(er)
  this.nuevaClase['color'] = er;
  }
  cancelarEdicion(){
  //  this.rows = this.originalData;
 // console.log(this.test);
  }



}



