
import { Component, OnInit, ElementRef, ViewChild } from '@angular/core';
import { EndpointsService } from '../endpoints.service';
import {FormBuilder, FormGroup, Validators} from "@angular/forms";
 import {Router,CanActivate,ActivatedRouteSnapshot,RouterStateSnapshot} from '@angular/router';

import {NgbModal, ModalDismissReasons, NgbActiveModal} from '@ng-bootstrap/ng-bootstrap';


@Component({
  selector: 'tox-parametros',
  templateUrl: './parametros.component.html',
  styleUrls: ['./parametros.component.scss']
})
export class ParametrosComponent implements OnInit {
 globalImage='http://50.116.17.150:3000/';
 form: FormGroup;
  form2: FormGroup;
   form3: FormGroup;

  @ViewChild('fileInput') fileInput: ElementRef;
    @ViewChild('fileInput2') fileInput2: ElementRef;
      @ViewChild('fileInput3') fileInput3: ElementRef;

rows = [];
    rowsUsuarios = [];
dataImagen='';

dataImagen2='';
dataImagen3='';

dataFile='';
dataFile2='';
dataFile3='';

url='';
actpass={};
modalReference: any;
  categorias = [];
  selected = [];
  superadmin=<any>{};
  editar = false;

    editar1 = false;
      editar2 = false;
        editar3 = false; 

  claseSeleccionada = <any>{};
  nuevaClase ={}; 
   cu = JSON.parse(localStorage.getItem('currentUser'));
   public loading = false;
   public loadingC = false;
public isCollapsed = false;



    rowsHorarios = [];

selectedHorario = [];
  columnsHorarios: any[] = [
    { prop: 'idReservaClase', name:'#ID Reserva'} , 
    { prop: 'soloFecha',  name: 'Fecha Clase' }, 
        { prop: 'soloHora',  name: 'Hora Clase' }, 
       { prop: 'estado',  name: 'Estado' }, 
         { prop: 'usuariosAnotados',  name: 'Usuarios Anotados' }, 


  ];


  originalData = [];

  columns: any[] = [
    { prop: 'nombre',  name: 'Nombre' }, 
    { prop: 'valor',  name: 'Valor' }
  ];


  constructor(public endpointsService: EndpointsService,public fb: FormBuilder,private router:Router,private modalService: NgbModal) { 
      this.createForm();
      this.createForm2();
      this.createForm3();


      if(this.cu[0]['tipo'] !== 3){
  console.log('usuario no deseado');
  this.router.navigate(['/dashboard']);
}

    this.dataImagen = this.globalImage+'uploads/1banner';
    this.dataImagen2 = this.globalImage+'uploads/2banner';
        this.dataImagen3 = this.globalImage+'uploads/3banner';


      }


  createForm() {
    this.form = this.fb.group({
      imageU2: null
    });
  }
  createForm2() {
    this.form2 = this.fb.group({
      imageU: null
    });
  }
  createForm3() {
    this.form3 = this.fb.group({
      imageU3: null
    });
  }


  ngOnInit() {
  	console.log('d');
this.getStaff();
this.getSAInfo();

  }

  getStaff(){

        this.endpointsService.getParametros()
    .then((data:any) => {    
      console.log(data);
      this.rows = data;
      this.originalData = data;
     });


  }

  getSAInfo(){

        this.endpointsService.getSAInfo()
    .then((data:any) => {    
      console.log(data);
      this.superadmin = data[0];
 
     });


  }




  cargaUsuarios(id){

  this.endpointsService.getUsuariosAnotadosClase({idReserva:id})
    .then((data:any) => {    
      //console.log(data);
      this.rowsUsuarios = data;
     // this.originalData = data;
     });


  }



  onFileChange(event,num) {

    if(event.target.files.length > 0) {
      let file = event.target.files[0];

    var reader = new FileReader();
    reader.onload = (event:any) => {

      if(num==1){this.dataImagen = event.target.result;}
      if(num==2){this.dataImagen2 = event.target.result;}
      if(num==3){this.dataImagen3 = event.target.result;}
      
    }
    reader.readAsDataURL(event.target.files[0]);

      if(num==1){    
      this.dataFile = file;
      console.log(this.dataFile);
      }
      if(num==2){    this.dataFile2 = file;}
      if(num==3){    this.dataFile3 = file;}

    }

  }

openVerticallyCentered(content) {
this.actpass={};
      this.modalReference = this.modalService.open(content);
  }

readUrl(event:any) {
  if (event.target.files && event.target.files[0]) {
    var reader = new FileReader();

    reader.onload = (event:any) => {
      this.url = event.target.result;
    }

    reader.readAsDataURL(event.target.files[0]);
  }
}


    prepareSave(num){

    let input = new FormData();
  
    var dataF;

    if(num==1){dataF = this.dataFile}
    if(num==2){dataF = this.dataFile2}
    if(num==3){dataF = this.dataFile3}

input.append('num', num);
    input.append(('image'), dataF);
    


    return input;
  }

  onSubmit(num) {
    const formModel = this.prepareSave(num);
    console.log(formModel);
    this.loadingC = true;

    this.endpointsService.subirImagen2(formModel)
    .then((data:any) => { 
        this.loadingC = false;   
        console.log(data);
        location.reload();
     });


    
  }

actualizarData(data){
  console.log(data);
        this.endpointsService.actualizarDataSA(data)
    .then((data:any) => {    
      if(data.affectedRows>0){
      alert('Actualizado correctamente');
      this.getSAInfo();
       this.modalReference.close();
      }
      else{
      alert('La contraseña no pudo ser actualizada');
        }
     });


}



  guardarNuevaClase(){

 this.loading = true;
 console.log(this.nuevaClase);

      this.endpointsService.nuevaCategoria(this.nuevaClase)
    .then((data:any) => {    
      console.log(data);
       this.loading = false;
       this.getStaff();
       alert('Guardado correctamente');
       this.editar = false;
       this.nuevaClase={};

     },  msg => { // Error
        console.log(msg);
        this.loading = false;
        });





  }

  
      cargaCategorias(){

        this.endpointsService.categoriasHome()
    .then((data:any) => {    
      console.log(data);
      this.categorias = data;
     });


  }



  onSelectHorario({ selectedHorario }) {
    console.log(this.selectedHorario[0]); 
   // this.rows = this.originalData;
   // this.test = JSON.parse(JSON.stringify(this.selected[0]));
   // this.claseSeleccionada = JSON.parse(JSON.stringify(this.selected[0]));
    this.cargaUsuarios(this.selectedHorario[0].idReservaClase);
   
  }



  onSelect({ selected }) {
    console.log(this.selected[0]);
    this.selectedHorario = []; 
   // this.rows = this.originalData;
   // this.test = JSON.parse(JSON.stringify(this.selected[0]));
    this.claseSeleccionada = JSON.parse(JSON.stringify(this.selected[0]));
    this.dataImagen = this.globalImage+this.claseSeleccionada['idFoto'];
   // this.cargaHorarios();
  }

  onActivate(event) {
    console.log('Activate Event', event);
  }


  guardarEdicion(){
 this.loading = true;
 console.log(this.claseSeleccionada);


  this.endpointsService.editarParametro(this.claseSeleccionada)
    .then((data:any) => {    
      console.log(data);
       this.loading = false;
       this.getStaff();
       alert('Editado correctamente');
       this.editar = false;

     },  msg => { // Error
        console.log(msg);
       // this.loading = false;
        });



  }



  onChangeColor2(er){
  console.log(er)
  this.claseSeleccionada['color'] = er;
  }


  onChangeColor(er){
  console.log(er)
  this.nuevaClase['color'] = er;
  }
  cancelarEdicion(){
  //  this.rows = this.originalData;
 // console.log(this.test);
  }



}



