getPrecioDescuento(precio){

  var retorno = 0;


  if(this.usuarioSeleccionado['descuento']){

    var descuento = this.usuarioSeleccionado['descuento'];
    var precioN = precio;
    retorno = precioN - (precioN*(descuento/100));

  }
  else{
    retorno = precio;
  }




  return retorno.toFixed(2);

}
