  updateFiltersss(event,i) {

    const val = event.target.value.toLowerCase();

    // filter our data
    const temp = this.filtroData[i].filter(function(d) {
      return d.nombreCliente.toLowerCase().indexOf(val) !== -1 || !val;
    });

    // update the rows
    this.diasRutina[i] = temp;
    // Whenever the filter changes, always go back to the first page
    //this.table.offset = 0;
  }

  updateFilter(event) {
  var f = this.posicionArr;
    var keys=['nombreCliente','nombreEmpleado', 'idCita'];
    const val = event.target.value.toLowerCase();
    console.log(val);
    // filter our data
    const temp = this.filtroData[f].filter(function(item) {

      //
 console.log(item);
         for (let i=0; i<3; i++){

      if (String(item[keys[i]]).toLowerCase().indexOf(val) !== -1 || !val){
        // found match, return true to add to result set
        return true;
      }

}
      //return d.nombreCliente.toLowerCase().indexOf(val) !== -1 || !val;
    });


    this.diasRutina[f] = temp;

  }


