import { Component, OnInit, ElementRef, ViewChild } from '@angular/core';
import { EndpointsService } from '../endpoints.service';
import {FormBuilder, FormGroup, Validators} from "@angular/forms";
  import {NgbModal, ModalDismissReasons, NgbActiveModal} from '@ng-bootstrap/ng-bootstrap';
import {Router,CanActivate,ActivatedRouteSnapshot,RouterStateSnapshot} from '@angular/router';

import * as $ from 'jquery';

window["$"] = $;
window["jQuery"] = $;


@Component({
  selector: 'tox-horario',
  templateUrl: './horario.component.html',
  styleUrls: ['./horario.component.scss']
})
export class HorarioComponent implements OnInit {
  cu = JSON.parse(localStorage.getItem('currentUser'));
  seccionActivas = localStorage.getItem('seccUBY');

modalReference: any;
horario=[];
especiales=[];
lunes =<any>{};
martes =<any>{};
miercoles =<any>{};
jueves =<any>{};
viernes =<any>{};
sabado =<any>{};
domingo =<any>{};
nuevaFecha =<any>{};
fechaFiltro2 = <any>{};
fechaFiltro = <any>{};
  constructor(public endpointsService: EndpointsService,public fb: FormBuilder, private router:Router,private modalService: NgbModal) {


if(this.cu[0]['tipo'] == 2){
if(!this.seccionActivas.split(',').includes("7")){
  console.log('usuario no deseado');
  this.router.navigate(['/dashboard']);

  }
}


  this.lunes = {diaSemana:1};
  this.martes = {diaSemana:2};
  this.miercoles = {diaSemana:3};
  this.jueves = {diaSemana:4};
  this.viernes = {diaSemana:5};
  this.sabado = {diaSemana:6};
  this.domingo = {diaSemana:0};


  this.getHorarios();

  var TodayDate = new Date();
var dd = TodayDate.getDay();
var mm = TodayDate.getMonth()+1;
var mm2 = TodayDate.getMonth();
var yy = TodayDate.getFullYear();

this.fechaFiltro2 = {year: yy, month: mm, day: dd};

   }




  ngOnInit() {

  }

  	pad2(number) {
	    return (number < 10 ? '0' : '') + number;
	}

	cambiarCB(event) {
	  console.log(event);
	}


  	nuevaFechaEspecial(nuevaFecha) {
	    nuevaFecha.idCentro=localStorage.getItem('idCentroBY');
	    //dia.estado = dia.estadoCheck == true ? 1 : 0;


		     var hAPF = this.fechaFiltro['year'] +'-'+this.pad2(this.fechaFiltro['month'])+'-'+this.pad2(this.fechaFiltro['day']);

	     var hAP = (nuevaFecha.horaAbrir ? this.pad2(nuevaFecha.horaAbrir.hour) : '00') +':'+(nuevaFecha.horaAbrir ? this.pad2(nuevaFecha.horaAbrir.minute) : '00' ) +':00';


	     var hCP = (nuevaFecha.horaCerrar ? this.pad2(nuevaFecha.horaCerrar.hour) : '00') +':'+(nuevaFecha.horaCerrar ? this.pad2(nuevaFecha.horaCerrar.minute) : '00' ) +':00';



    	if(nuevaFecha.estadoCheck == true){
    	nuevaFecha.abierto = 0;
    	nuevaFecha.fecha = hAPF;
	    nuevaFecha.horaAbrir = '00:00:00';
	    nuevaFecha.horaCerrar = '00:00:00';

    	}
	    else{
	    nuevaFecha.abierto = 1;
		 nuevaFecha.fecha = hAPF;
	     nuevaFecha.horaAbrir = hAP;
	     nuevaFecha.horaCerrar = hCP;
	    }


	     



		console.log(nuevaFecha);


        this.endpointsService.nuevaFechaEspecial(nuevaFecha)
    .then((data:any) => {    
    	if(data.affectedRows>0){
    	alert('Agregado correctamente');
    	this.modalReference.close();
    	this.getHorarios();

    	}
    	else{alert('Ha ocurrido un error inesperado');this.getHorarios();}
      console.log(data);

     });



	}

  	guardarDiaEspecial(dia) {
	    dia.idCentro=localStorage.getItem('idCentroBY');
	    //dia.estado = dia.estadoCheck == true ? 1 : 0;
	    if(dia.estadoCheck == true){dia.abierto = 0}
	    else{dia.abierto = 1}

	     var hAP = this.pad2(dia.horaAbrir.hour) +':'+this.pad2(dia.horaAbrir.minute)+':00';

	     var hCP = this.pad2(dia.horaCerrar.hour) +':'+this.pad2(dia.horaCerrar.minute)+':00';
	     dia.horaAbrir = hAP;
	     dia.horaCerrar = hCP;
		console.log(dia);


        this.endpointsService.actualizarFechaEspecial(dia)
    .then((data:any) => {    
    	if(data.affectedRows>0){
    	alert('Editado correctamente');
    	this.getHorarios();
    	}
    	else{alert('Ha ocurrido un error inesperado');this.getHorarios();}
      console.log(data);

     });



	}

  agregarFecha(content) {

    this.modalReference = this.modalService.open(content);
  }


  	borrarFechaEspecial(dia) {

        this.endpointsService.borrarFechaEspecial({idHorarioEspecial:dia})
    .then((data:any) => {    
    	if(data.affectedRows>0){
    	alert('Eliminado correctamente');
    	this.getHorarios();
    	}
    	else{alert('Ha ocurrido un error inesperado');
    	this.getHorarios();}
     // console.log(data);

     });

  	}

  	guardarHorario(dia) {
	    dia.idCentro=localStorage.getItem('idCentroBY');
	    //dia.estado = dia.estadoCheck == true ? 1 : 0;
	    if(dia.estadoCheck == true){dia.estado = 0}
	    else{dia.estado = 1}

	     var hAP = this.pad2(dia.horaAbrir.hour) +':'+this.pad2(dia.horaAbrir.minute)+':00';

	     var hCP = this.pad2(dia.horaCerrar.hour) +':'+this.pad2(dia.horaCerrar.minute)+':00';
	     dia.horaAbrir = hAP;
	     dia.horaCerrar = hCP;
		console.log(dia);


        this.endpointsService.actulizarHorario(dia)
    .then((data:any) => {    
    	if(data.affectedRows>0){
    	alert('Editado correctamente');
    	this.getHorarios();
    	}
    	else{alert('Ha ocurrido un error inesperado');this.getHorarios();}
      console.log(data);

     });



	}



    getHorarios(){

        this.endpointsService.getHorarios2({idCentro:localStorage.getItem('idCentroBY')})
    .then((data:any) => {    
      console.log(data);

	data['horario'].forEach((item, index) => {
	console.log(item);

		var hA = {
		"hour": item.horaAbrir.split(':')[0],
		"minute": item.horaAbrir.split(':')[1]
		};


		item.horaAbrir= hA;

				var hC = {
		"hour": item.horaCerrar.split(':')[0],
		"minute": item.horaCerrar.split(':')[1]
		};
		item.horaCerrar= hC;


		item.editar=true;

		if(item.estado == 0){item.estadoCheck = true}
		else{item.estadoCheck = false}
		

		if(item.diaSemana == 0){

		this.domingo = item;




		}
		if(item.diaSemana == 1){this.lunes = item;}
		if(item.diaSemana == 2){this.martes = item; }
		if(item.diaSemana == 3){this.miercoles = item;}
		if(item.diaSemana == 4){this.jueves = item;}
		if(item.diaSemana == 5){this.viernes = item;}
		if(item.diaSemana == 6){this.sabado = item;}

	});


      this.horario = data['horario'];

      var ss =[];
      	data['especiales'].forEach((item, index) => {
	//console.log(item);

		var hA = {
		"hour": item.horaAbrir.split(':')[0],
		"minute": item.horaAbrir.split(':')[1]
		};


		item.horaAbrir= hA;

				var hC = {
		"hour": item.horaCerrar.split(':')[0],
		"minute": item.horaCerrar.split(':')[1]
		};
		item.horaCerrar= hC;


		item.editar=true;

		if(item.abierto == 0){item.estadoCheck = true}
		else{item.estadoCheck = false}
		


		ss.push(item);



	});



      this.especiales = ss;

     });


  }




}



