import { Component, OnInit, Renderer2, HostListener } from '@angular/core';
import {Router} from '@angular/router';
 import { EndpointsService } from '../../../endpoints.service';
@Component({
  selector: 'tox-headerbar',
  templateUrl: './headerbar.component.html',
})
export class HeaderbarComponent implements OnInit {

  sidebarVisible: boolean;

  @HostListener('window:resize', ['$event'])
  onResize(event) {
    this.setSidebar(event);
  }


  constructor(private renderer: Renderer2, private router:Router, public endpointsService: EndpointsService) {
  

  }
  
currentUser=<any>{};
seccionesUser=[];
  ngOnInit(): void {
    this.currentUser = JSON.parse(localStorage.getItem('currentUser'))[0];
    this.getSeccionesUsuario();
    //this.seccionesUser = localStorage.getItem('seccUBY').split(',');
    //data[0]['secciones'].split(',')
    //console.log(this.seccionesUser);
    this.setSidebarByWidth(window.innerWidth);
  }

  toggleSidebar() {
    if (this.sidebarVisible) {
      this.hideSidebar();
    } else {
      this.showSidebar();
    }
  }

getSeccionesUsuario(){
      this.endpointsService.getSeccionesUsuario({idUsuarioConsola:this.currentUser['idUsuarioConsola']})
    .then((data:any) => {

        if(data[0]){
       localStorage.setItem( 'seccUBY', data[0]['secciones'] );
      this.seccionesUser = data[0]['secciones'].split(',');
      
        }
        else{
        localStorage.setItem( 'seccUBY', '');
         this.seccionesUser = [];
        }
       

       
     // console.log(this.seccionesUser) ;




    })

}

  setSidebar(event) {
    const width = (event.target.innerWidth > 0) ? event.target.innerWidth : event.screen.width;
    this.setSidebarByWidth(width);
  }


  cerrarSesion() {
     console.log('cerrar');
     localStorage.removeItem('currentUser');
     localStorage.removeItem('idCentroBY');
     localStorage.removeItem('seccUBY');
     
     this.router.navigate(['/login']);
  }




  setSidebarByWidth(width) {
    if (width < 992) {
      this.hideSidebar();
    } else {
      this.showSidebar();
    }
  }

  showSidebar() {
    this.sidebarVisible = true;
    this.renderer.addClass(document.body, 'show-sidebar');
  }

  hideSidebar() {
    this.sidebarVisible = false;
    this.renderer.removeClass(document.body, 'show-sidebar');
  }
}



