import { Component, OnInit, ElementRef, ViewChild } from '@angular/core';
import { EndpointsService } from '../endpoints.service';
import {FormBuilder, FormGroup, Validators} from "@angular/forms";
import {Router,CanActivate,ActivatedRouteSnapshot,RouterStateSnapshot} from '@angular/router';

import { MouseEvent } from '@agm/core';

@Component({
  selector: 'tox-centro',
  templateUrl: './centro.component.html',
  styleUrls: ['./centro.component.scss']
})





export class CentroComponent implements OnInit {

globalImage='http://50.116.17.150:3000/';
 form: FormGroup;
  @ViewChild('fileInput') fileInput: ElementRef;
   @ViewChild('fileInputB') fileInputB: ElementRef;

  cu = JSON.parse(localStorage.getItem('currentUser'));
  seccionActivas = localStorage.getItem('seccUBY');

dataCentro =<any>{};
ventasCentro  =<any>{};
  constructor(public endpointsService: EndpointsService,public fb: FormBuilder,private router:Router) { 

if(this.cu[0]['tipo'] == 2){
if(!this.seccionActivas.split(',').includes("5")){
  console.log('usuario no deseado');
  this.router.navigate(['/dashboard']);

  }
}
/*
if(this.cu[0]['tipo'] !== 1){
  console.log('usuario no deseado');
  this.router.navigate(['/dashboard']);
}
*/
  this.createForm();

  }
  dataFile='';
    dataFileB='';
  zoom: number = 16;
  
  // initial center position for the map
  lat: number = 51.673858;
  lng: number = 7.815982;

  clickedMarker(label: string, index: number) {
    console.log(`clicked the marker: ${label || index}`)
  }
  
  mapClicked($event: MouseEvent) {

  if(this.editarOn){
  this.dataCentro['longitud'] =$event.coords.lng;
  this.dataCentro['latitud']=$event.coords.lat;


    this.markers[0]={
      lat: $event.coords.lat,
      lng: $event.coords.lng,
      label: 'Nueva Ubicacion',
      draggable: true
    };
    }
  }

     /* 
     {
      lat: 51.673858,
      lng: 7.815982,
      label: 'A',
      draggable: true
    }
  */
  markerDragEnd(m: marker, $event: MouseEvent) {
    console.log('dragEnd', m, $event);
  }
  
  markers: marker[] = [];



    createForm() {
    this.form = this.fb.group({
      imageU: null,
      imageB: null
    });
  }



clientes =[];


editarOn = false;
  ngOnInit() {

  this.getInfoCentro();


  }

    getInfoCentro(){

        this.endpointsService.getInfoCentro({idCentro:localStorage.getItem('idCentroBY')})
    .then((data:any) => {    
      console.log(data);
      this.dataCentro = data.info[0];
      this.dataCentro['imageLogo'] = this.globalImage+this.dataCentro['idFoto'];
      this.dataCentro['imageB'] = this.globalImage+this.dataCentro['imagenBanner'];
      this.clientes = data.clientes;
       this.ventasCentro = data.dataV[0];
       this.editarOn = false;


        this.markers[0]={
      lat: this.dataCentro['latitud'],
      lng: this.dataCentro['longitud'],
      label: this.dataCentro['nombre'],
      draggable: true
    }


    
     });


  }



  onFileChange(event) {
    if(event.target.files.length > 0) {
      let file = event.target.files[0];
      //this.form.get('imageU').setValue(file);

    var reader = new FileReader();
    reader.onload = (event:any) => {
      this.dataCentro['imageLogo'] = event.target.result;
    }
    reader.readAsDataURL(event.target.files[0]);
    this.dataFile = file;
    }
  }
  onFileChangeB(event) {
    if(event.target.files.length > 0) {
      let file = event.target.files[0];
      //this.form.get('imageU').setValue(file);

    var reader = new FileReader();
    reader.onload = (event:any) => {
      this.dataCentro['imageB'] = event.target.result;
    }
    reader.readAsDataURL(event.target.files[0]);
    this.dataFileB = file;
    }
  }


prepareSave(centro){

    let input = new FormData();
    
    if(this.dataFile){
    input.append('imageU', this.dataFile);
    }
    if(this.dataFileB){
    input.append('imageB', this.dataFileB);
    }
    
    
    input.append('nombre', centro.nombre);
    input.append('email', centro.email);
    input.append('fbLink', centro.fbLink);
    input.append('latitud', centro.latitud);
    input.append('longitud', centro.longitud);
    input.append('horarioAppBanner', centro.horarioAppBanner);
    input.append('sobreNosotros', centro.sobreNosotros);
    input.append('direccion', centro.direccion);
    input.append('telefono', centro.telefono);
    input.append('idCentro', centro.idCentro);
        input.append('tipoReserva', centro.tipoReserva);

    return input;
  }


      actualizarDatos(){
      const formModel = this.prepareSave(this.dataCentro);
        this.endpointsService.actualizarDCentro(formModel)
    .then((data:any) => {    
      if(data.affectedRows && data.affectedRows > 0 ){
        alert('Editado correctamente');
          this.getInfoCentro();
      }
      else{

      alert('Ha ocurrido un error');
      }

    
     });


      console.log(this.dataCentro);

  }



}

interface marker {
  lat: number;
  lng: number;
  label?: string;
  draggable: boolean;
}



