import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';


// Layouts
import { LayoutAdminComponent } from './shared/components/layout-admin/layout-admin.component';
// import { LayoutSimpleComponent } from './shared/layouts/simple/simple.component';


import {GestionclasesComponent} from './gestionclases/gestionclases.component';

import {ProgramarClaseComponent} from './programar-clase/programar-clase.component';

import {UsuariosComponent} from './usuarios/usuarios.component';
import {LoginComponent} from './login/login.component';

import {AsignarComponent} from './asignar/asignar.component';

import {OpinionesComponent} from './opiniones/opiniones.component';
import {CentroComponent} from './centro/centro.component';
import {VentasComponent} from './ventas/ventas.component';
import {ManejousuariosComponent} from './manejousuarios/manejousuarios.component';
import {StafComponent} from './staf/staf.component';

import {EjerciciosComponent} from './ejercicios/ejercicios.component';
import {HorarioComponent} from './horario/horario.component';

import {RutinaComponent} from './rutina/rutina.component';
import {OfertasComponent} from './ofertas/ofertas.component';

import {AdmincentrosComponent} from './admincentros/admincentros.component';
import {AdminusersComponent} from './adminusers/adminusers.component';
import {AddcategoriasComponent} from './addcategorias/addcategorias.component';

import {ParametrosComponent} from './parametros/parametros.component';

import {ProfesoresComponent} from './profesores/profesores.component';
import {MaquinasComponent} from './maquinas/maquinas.component';
import {NovedadesComponent} from './novedades/novedades.component';
import {UsuarioadminComponent} from './usuarioadmin/usuarioadmin.component';
import { AuthguardService }  from './service/authguard.service';


const routes: Routes = [
  /*
  {
    path: '',
    redirectTo: 'dashboard',
    pathMatch: 'full',
  },
  */

    {
    path: 'login',
    component: LoginComponent
  },
  {
    path: '',
    component: LayoutAdminComponent,canActivate: [AuthguardService],
    children: [
        {
        path: '',
        redirectTo: 'dashboard',
        pathMatch: 'full',
      },
      {
        path: 'dashboard',
        loadChildren: './dashboard/dashboard.module#DashboardModule'
      },
      {
        path: 'plong',
        loadChildren: './plong/plong.module#PlongModule'
      },

      {
        path: 'servicios',
        component: GestionclasesComponent
       // loadChildren: './plong/plong.module#PlongModule'
      },

            {
        path: 'calendario',
        component: ProfesoresComponent
       // loadChildren: './plong/plong.module#PlongModule'
      },


            {
        path: 'usuarios',
        component: UsuariosComponent
       // loadChildren: './plong/plong.module#PlongModule'
      },

              {
        path: 'asignar',
        component: AsignarComponent
       // loadChildren: './plong/plong.module#PlongModule'
      },


            {
        path: 'citas',
        component: EjerciciosComponent
       // loadChildren: './plong/plong.module#PlongModule'
      },
            {
        path: 'horario',
        component: HorarioComponent
       // loadChildren: './plong/plong.module#PlongModule'
      },

            {
        path: 'cupones',
        component: RutinaComponent,
        loadChildren: './plong/plong.module#PlongModule'
      },

        {
        path: 'uconsola',
        component: ManejousuariosComponent
       // loadChildren: './plong/plong.module#PlongModule'
      },


        {
        path: 'adminusers',
        component: AdminusersComponent
       // loadChildren: './plong/plong.module#PlongModule'
      },  

      {
        path: 'parametros',
        component: ParametrosComponent
       // loadChildren: './plong/plong.module#PlongModule'
      },
  {
        path: 'addcategorias',
        component: AddcategoriasComponent
       // loadChildren: './plong/plong.module#PlongModule'
      },

        {
        path: 'uapp',
        component: UsuarioadminComponent
       // loadChildren: './plong/plong.module#PlongModule'
      },

        {
        path: 'admincentros',
        component: AdmincentrosComponent
       // loadChildren: './plong/plong.module#PlongModule'
      },




                  {
        path: 'profesores',
        component: ProfesoresComponent
       // loadChildren: './plong/plong.module#PlongModule'
      },

                        {
        path: 'centro',
        component: CentroComponent
       // loadChildren: './plong/plong.module#PlongModule'
      },

                        {
        path: 'ventas',
        component: VentasComponent
       // loadChildren: './plong/plong.module#PlongModule'
      },

                              {
        path: 'staf',
        component: StafComponent
       // loadChildren: './plong/plong.module#PlongModule'
      },


                              {
        path: 'ofertas',
        component: OfertasComponent
       // loadChildren: './plong/plong.module#PlongModule'
      },


                        {
        path: 'opiniones',
        component: OpinionesComponent
       // loadChildren: './plong/plong.module#PlongModule'
      },



                  {
        path: 'maquinas',
        component: MaquinasComponent
       // loadChildren: './plong/plong.module#PlongModule'
      },

                        {
        path: 'novedades',
        component: NovedadesComponent
       // loadChildren: './plong/plong.module#PlongModule'
      },




      // {
      //   path: 'offerassets',
      //   loadChildren: './offerassets/offerassets.module#OfferassetsModule'
      // },
      // {
      //   path: 'offersetups',
      //   loadChildren: './offersetups/offersetups.module#OffersetupsModule'
      // },
      // {
      //   path: 'offertemplates',
      //   loadChildren: './offertemplates/offertemplates.module#OffertemplatesModule'
      // },
      // {
      //   path: 'offerteasers',
      //   loadChildren: './offerteasers/offerteasers.module#OfferteasersModule'
      // },
    ]
  },
  // {
  //   path: '',
  //   component: LayoutSimpleComponent,
  //   children: [
  //     {
  //       path: 'error',
  //       loadChildren: './error/error.module#ErrorModule'
  //     },
  //   ]
  // },
  {path: '**', redirectTo: 'error'}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule {
}



