    guardarHorario(dia) {
      dia.idEmpleado=this.claseSeleccionada['idEmpleado'];
      //dia.estado = dia.estadoCheck == true ? 1 : 0;

      if(dia.estadoCheck == true){

      dia.estado = 0;

      }
      else{

      dia.estado = 1;

      }

       var hAP = (dia.horaEntrar ? this.pad2(dia.horaEntrar['hour']) : '00') +':'+(dia.horaEntrar ? this.pad2(dia.horaEntrar['minute']) : '00')+':00';

       var hCP = (dia.horaSalir ? this.pad2(dia.horaSalir['hour']) : '00') +':'+(dia.horaSalir ? this.pad2(dia.horaSalir['minute']) : '00')+':00';
       dia.horaEntrar = hAP;
       dia.horaSalir = hCP;
    console.log(dia);


        this.endpointsService.actualizarHorarioEmpleado(dia)
    .then((data:any) => {    
      if(data.affectedRows>0){
      alert('Editado correctamente');
      this.getHorarioEmpleado(this.claseSeleccionada['idEmpleado']);
      }
      else{alert('Ha ocurrido un error inesperado');
        this.getHorarioEmpleado(this.claseSeleccionada['idEmpleado']);}
      console.log(data);

     });



  }


    getHorarioEmpleado(idEmpleadoD){

        this.endpointsService.getHorarioEmpleado({idEmpleado:idEmpleadoD})
    .then((data:any) => {    
      console.log(data);

  data['horario'].forEach((item, index) => {
  console.log(item);

    var hA = {
    "hour": item.horaEntrar.split(':')[0],
    "minute": item.horaEntrar.split(':')[1]
    };


    item.horaEntrar= hA;

        var hC = {
    "hour": item.horaSalir.split(':')[0],
    "minute": item.horaSalir.split(':')[1]
    };
    item.horaSalir= hC;


    item.editar=true;

    if(item.estado == 0){item.estadoCheck = true}
    else{item.estadoCheck = false}
    

    if(item.diaSemana == 0){

    this.domingo = item;




    }
    if(item.diaSemana == 1){this.lunes = item;}
    if(item.diaSemana == 2){this.martes = item; }
    if(item.diaSemana == 3){this.miercoles = item;}
    if(item.diaSemana == 4){this.jueves = item;}
    if(item.diaSemana == 5){this.viernes = item;}
    if(item.diaSemana == 6){this.sabado = item;}

  });


      this.horario = data['horario'];

      var ss =[];
        data['especiales'].forEach((item, index) => {
  //console.log(item);

    var hA = {
    "hour": item.horaEntrar.split(':')[0],
    "minute": item.horaEntrar.split(':')[1]
    };


    item.horaEntrar= hA;

        var hC = {
    "hour": item.horaSalir.split(':')[0],
    "minute": item.horaSalir.split(':')[1]
    };
    item.horaSalir= hC;


    item.editar=true;

    if(item.abierto == 0){item.estadoCheck = true}
    else{item.estadoCheck = false}
    


    ss.push(item);



  });



      this.especiales = ss;

     });


  }


