import { Component, OnInit } from '@angular/core';
import { EndpointsService } from '../endpoints.service';



@Component({
  selector: 'tox-asignar',
  templateUrl: './asignar.component.html',
  styleUrls: ['./asignar.component.scss']
})
export class AsignarComponent implements OnInit {



rutinaUsuario = [];
rows = [];
selected = [];
  editar = false;
    usuarioSeleccionado = {};
  nuevoUsuario ={}; 
     public loading = false;
public isCollapsed = false;
  constructor(public endpointsService: EndpointsService) { }



  ngOnInit() {

  this.cargaUsuarios();
  this.cargaRutinas();
  }



    cargaUsuarios(){

        this.endpointsService.getUsuariosT()
    .then((data:any) => {    
     // console.log(JSON.parse(data));
            console.log(data);
      this.rows = data;
   

     });


  }


    getRutinaUsuario(){
//console.log(this.usuarioSeleccionado.idUsuario);
console.log(this.usuarioSeleccionado['idUsuario']);


        this.endpointsService.getRutinaUsuario(this.usuarioSeleccionado['idUsuario'])
    .then((data:any) => {    
      ////console.log(data);
      this.rutinaUsuario = data;
      if(this.rutinaUsuario[0]){
      this.usuarioSeleccionado['idRutina'] = this.rutinaUsuario[0]['idRutina'];
      }
      
     });


  }




  onSelect({ selected }) {
    console.log(this.selected[0]);
     this.usuarioSeleccionado = JSON.parse(JSON.stringify(this.selected[0]));
     this.getRutinaUsuario();
  }




  guardarEdicion(){
 this.loading = true;



 console.log(this.usuarioSeleccionado);
if(this.rutinaUsuario[0]){

	this.endpointsService.editarRutinaUsuario(this.usuarioSeleccionado['idRutina'],this.rutinaUsuario[0].idRutinaEstado)
    .then((data:any) => {    
      console.log(data);
       this.loading = false;

       this.getRutinaUsuario();
         this.cargaUsuarios();
       alert('Editado correctamente');
       this.editar = false;

     },  msg => { // Error
        console.log(msg);
       // this.loading = false;
        });
}
else{
	

	this.endpointsService.asignarRutinaUsuario(this.usuarioSeleccionado['idUsuario'],this.usuarioSeleccionado['idRutina'])
    .then((data:any) => {    
      console.log(data);
       this.loading = false;
       this.getRutinaUsuario();
         this.cargaUsuarios();
       alert('Guardado correctamente');
       this.editar = false;

     },  msg => { // Error
        console.log(msg);
       // this.loading = false;
        });


}
}




  getCellClass({ row, column, value }): any {
    return {

      'class1': (value) === 1,
      'parpadea': (value) === 1

    };
  }


    getRowClass(row) {
    return {
      'class1': (row.estadoRutina) === 1,
      'parpadea': (row.estadoRutina) === 1
    };
  }

	pad2(number) {
	    return (number < 10 ? '0' : '') + number;
	}


    cargaRutinas(){

        this.endpointsService.getRutinas()
    .then((data:any) => {    
     // //console.log(data);
      this['clases'] = data;

     // this.originalData = data;
     });


  }





  guardarUsuario(){

 this.loading = true;


   this.nuevoUsuario['fechaNacimiento'] = this.nuevoUsuario['fecha']["year"]+'-'+ 
    								 this.pad2(this.nuevoUsuario['fecha']["month"])+'-'+
    								 this.pad2(this.nuevoUsuario['fecha']["day"])
    								 '00:00:00';


 console.log(this.nuevoUsuario);

      this.endpointsService.nuevoUsuario(this.nuevoUsuario)
    .then((data:any) => {    
      console.log(data);
       this.loading = false;
       this.cargaUsuarios();
       alert('Guardado correctamente');
       this.editar = false;
       this.nuevoUsuario={};

     },  msg => { // Error
        console.log(msg);
        this.loading = false;
        });
}



}



