import { Component, OnInit } from '@angular/core';
import { EndpointsService } from '../endpoints.service';


@Component({
  selector: 'tox-maquinas',
  templateUrl: './maquinas.component.html',
  styleUrls: ['./maquinas.component.scss']
})
export class MaquinasComponent implements OnInit {




rows = [];
selected = [];
  editar = false;
    usuarioSeleccionado = {};
  nuevoUsuario ={}; 
     public loading = false;
public isCollapsed = false;
  constructor(public endpointsService: EndpointsService) { }



  ngOnInit() {

  this.cargaUsuarios();
  }



    cargaUsuarios(){

        this.endpointsService.getEquipamentoAll()
    .then((data:any) => {    
    console.log(data);
      this.rows = data.data;
    
     });


  }


  onSelect({ selected }) {
    console.log(this.selected[0]);
    this.usuarioSeleccionado = JSON.parse(JSON.stringify(this.selected[0]));
  
  }



  guardarEdicion(){
 this.loading = true;

 console.log(this.usuarioSeleccionado);

      this.endpointsService.editarMaquina(this.usuarioSeleccionado)
    .then((data:any) => {    
      console.log(data);
       this.loading = false;
       this.cargaUsuarios();
       alert('Editado correctamente');
       this.editar = false;

     },  msg => { // Error
        console.log(msg);
       // this.loading = false;
        });
}

	pad2(number) {
	    return (number < 10 ? '0' : '') + number;
	}




  guardarUsuario(){

 this.loading = true;


 console.log(this.nuevoUsuario);


      this.endpointsService.nuevaMaquina(this.nuevoUsuario)
    .then((data:any) => {    
      console.log(data);
       this.loading = false;
       this.cargaUsuarios();
       alert('Guardado correctamente');
       this.editar = false;
       this.nuevoUsuario={};

     },  msg => { // Error
        console.log(msg);
        this.loading = false;
        });
}



}



